@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.page{background:#000;width:100%;height:100vh;caret-color:transparent}.title{font-weight:bold;font-size:52px}
.avatar{position:relative;margin-bottom:10px}.avatar__wrapper{width:220px;height:220px;border:8px solid #fff;border-radius:160px;background:#edf2f4;position:relative;overflow:hidden}.avatar--outined{border:8px solid #d90429}.avatar__refresh{width:60px;height:60px;background:#fff;position:absolute;top:180px;left:180px;border-radius:50%;border:none;cursor:pointer;display:flex;justify-content:center;align-items:center}.avatar__face{width:100%}
.button{height:56px;border-radius:20px;padding:0 72px;font-size:24px;background:#d90429;border:none;font-weight:bold;cursor:pointer}.button_disabled{pointer-events:none;background:#8d99ae}.button:hover{background:#ee0830;box-shadow:3px 4px 10px rgba(217,4,41,.36)}
.text{color:#fff;font-family:"Montserrat",sans-serif}
.home{display:flex;flex-direction:column;align-items:center}.home__container{max-width:717px;width:100%;display:flex;flex-direction:column;align-items:center;height:100%;justify-content:space-around}.home__avatar_container{width:100%;display:flex;flex-wrap:wrap;justify-content:space-around}.home__pseudo{height:48px;border-radius:5px;font-size:18px;font-weight:500;text-align:center;caret-color:#000}.home__pseudo_container{height:220px;display:flex;flex-direction:column;justify-content:space-between;margin-bottom:30%}@media(max-width: 541px){.avatar_container{margin-bottom:0px;align-items:center}}
